.centered-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh; /* 화면 높이에 맞게 조정 */
  }
  
  .btn-15 {
    position: relative; /* 버튼 상대위치 지정 */
    overflow: hidden; /* 넘치는 요소 감추기 */
    z-index: 1;
    transition: color 0.3s; /* 색상 변경에 트랜지션 적용 */
    margin: 10px;
    border: none;
    background: none;
  }
  
  .btn-15:after {
    position: absolute;
    content: '';
    width: 0;
    height: 100%;
    top: 0;
    right: 100%; /* 초기에 버튼 왼쪽에 숨김 */
    z-index: -1;
    background-color: #663dff;
    border-radius: 5px;
    transition: all 0.3s ease;
  }
  
  .btn-15:hover:after {
    left: 0; /* 버튼의 왼쪽에 보이도록 변경 */
    width: 100%; /* 전체 너비 */
  }
  
  .btn-15:hover {
    color: #fff;
  }
  
  .btn-15:hover::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: -100%; /* 초기에 숨김 */
    z-index: -1;
    background-color: #663dff;
    border-radius: 5px;
    transition: all 0.3s ease;
  }
  
  .btn-15:hover::before {
    left: 0; /* 버튼의 왼쪽에 보이도록 변경 */
  }
  
  .content-container {
    /* 선택된 컨텐츠 컨테이너 스타일 */
  }
  