/* Start.css */
.start-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; /* 화면 전체 높이로 설정 */
    background-color: black;
    color: white;
  }
  
  /* 이미지 스타일 */
  .start-container img {
    max-width: 100%;
    height: auto;
    margin-bottom: 20px; /* 이미지와 텍스트 사이에 여백 추가 */
  }
  /* Start.css */
  .start-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .centered-image {
    max-width: 100%;
    max-height: 80vh; /* 화면 높이의 80%로 설정 */
    object-fit: contain; /* 이미지 비율 유지하면서 채워짐 */
  }
  