/* login.css */

body {
    background-color: lavenderblush;
  }
  
  .Login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    max-width: 100%;
  }
  
  .header-img {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 80%;
    max-width: 800px;
    margin-bottom: 10px;
  }
  
  .header-img img {
    width: calc(35.66% - 10px); /* 33.33%의 반 */
    height: 70%; /* 50%의 반 */
    border-radius: 10px;
    object-fit: cover;
    margin-top: 30px;
  }
  
  #jump {
    margin: 10px;
  }
  
  .hiddenText {
    display: none;
  }
  
  span:hover .hiddenText {
    display: inline;
    color: gray;
  }
  
  #hint {
    color: gray;
    position: absolute;
    bottom: 10px;
    font-size: 14px;
  }
  
  #hint button {
    font-size: 12px;
    border-radius: 10px;
  }
  
  .form-group {
    text-align: center;
  }
  
  .form-group p {
    margin: 5px 0;
  }
  
  .img-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 80%;
    max-width: 800px;
    margin-top: 20px;
  }
  
  .img-container img {
    width: calc(38.33% - 20px);
    height: 50%;
    border-radius: 10px;
    object-fit: cover;
  }
  
  h2 {
    margin-top: -10px;
  }
  
  /* login.css */
  
  #one {
    border-radius: 50px;
    transition: background-color 0.3s, transform 0.3s;
  }
  
  #one:active {
    background-color: #4caf50; /* 클릭 시 배경색 변경 */
    transform: scale(0.95); /* 클릭 시 크기 축소 효과 */
  }
  
  /* ... (기존 CSS 코드) */
  
  .wrap {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .button {
    width: 45px;
    height: 25px;
    font-family: 'Roboto', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
  }
  
  .button:hover {
    background-color: #2ee59d;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    transform: translateY(-7px);
  }
  
  .form-group {
    text-align: center;
  }
  
  .form-group p {
    margin: 5px 0;
    color: red; /* (ﾉ｀Д´) 텍스트의 색상을 빨간색으로 설정 */
  }
  
  .input-button-container {
    display: flex;
    align-items: center;
  }
  
  #jump {
    margin: 10px;
  }
  